.App {
  display: flex;
  flex-direction: column;
  background-color: #F6F8FA;
  height: 100%;
}

.content-container {
  display: flex;
  justify-content: space-between;
  padding: 2rem;
  height: 100%;
}
.graph {
  background: white;
  box-shadow: 0 5px 10px 0 #e3e8ef;
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
  padding: 1rem;
}

.graph--container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.graph--text {
  font-size: 14px;
  font-weight: 300;
  color: #9DA6BD;
}

.graph--months {
  display: flex;
  justify-content: space-between;
  max-width: 750px;
  width: 100%;
}

svg {
  background-color: white;
}

.retail-sales {
  stroke: #5CA6F0;
  stroke-width: 3px;
  fill: none;
}

.wholesale-sales {
  stroke: #9DA6BD;
  stroke-width: 3px;
  fill: none;
}
.header {
  display: flex;
  align-items: center;
  background-color: white;
  border-bottom: 1px solid #eaeef3;
  display: flex;
  padding: 1rem;
}

.header--title {
  font-size: 25px;
  font-weight: 600;
  margin: 0;
}
.product-container {
  background-color: white;
  box-shadow: 0 5px 10px 0 #e3e8ef;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  height: auto;
  width: 25%;
}

.tag-container {
  display: flex;
  flex-wrap: wrap;
  border-bottom: 1px solid #e3e8ef;
  border-top: 1px solid #e3e8ef;
  justify-content: space-between;
  width: 100%;
}
.tag {
  border: 1px solid #e3e8ef;
  border-radius: 5px;
  display: flex;
  padding: 3px 15px;
  margin: 10px 0;
}

.tag--name {
  color: #344052;
  font-size: 12px;
  font-weight: 300;
}
.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 1rem;
}

.product--image {
  width: 125px;
}

.product--title {
  color: #344052;
  margin: .5rem;
}

.product--subtitle {
  color: #9DA6BD;
  font-size: 14px;
  font-weight: 300;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

